<script>
  export let label = '';
  export let value = '2'; // "male", "female", "other"
  export let options = [
    { label: 'Nam', value: '1' },
    { label: 'Nữ', value: '0' },
    { label: 'Khác', value: '2' }
  ];

  function update(e) {
    value = e.target.value;
  }
</script>

<div class="flex flex-col gap-1">
  {#if label}
    <label class="text-sm font-medium text-gray-700">{label}</label>
  {/if}
  <select bind:value on:change={update} class="border rounded px-2 py-1 text-sm">
    {#each options as opt}
      <option value={+opt.value}>{opt.label}</option>
    {/each}
  </select>
</div>
