<script>
  import dayjs from 'dayjs';

  export let label = '';
  export let value = null; // có thể là datetime string hoặc milliseconds
  export let showAge = true;
  export let examDate = null; // ngày khám (datetime string hoặc milliseconds)

  $: birth = value ? dayjs(Number(value) || value) : null;
  $: exam = examDate ? dayjs(Number(examDate) || examDate) : dayjs();

  $: formatted = birth ? birth.format('DD/MM/YYYY') : '';
  $: age = birth ? exam.diff(birth, 'year') : null;
  $: full = birth ? (showAge && age !== null ? `${formatted} (${age} tuổi)` : formatted) : '';
</script>

<div class="flex flex-col gap-1">
  {#if label}
    <label class="text-sm font-medium text-gray-700">{label}</label>
  {/if}
  <input type="text" readonly class="border rounded px-2 py-1 text-sm bg-gray-50" value={full} />
</div>
