<script>
  import dayjs from 'dayjs';

  export let label = '';
  export let value = null; // có thể là string (ISO), hoặc number (ms)
  export let placeholder = '';
  export let disabled = false;
  export let mode = 'datetime'; // "datetime" hoặc "millisecond"

  // Format date cho input[type=datetime-local]
  function formatDate(val) {
    if (!val) return '';
    if (mode === 'millisecond') {
      return dayjs(Number(val)).format('YYYY-MM-DDTHH:mm');
    } else {
      return dayjs(val).format('YYYY-MM-DDTHH:mm');
    }
  }

  function parseDate(str) {
    if (!str) return null;
    const d = dayjs(str);
    if (!d.isValid()) return null;

    return mode === 'millisecond' ? d.valueOf() : d.toISOString();
  }

  let internalValue = formatDate(value);

  $: internalValue = formatDate(value);

  function onInput(e) {
    value = parseDate(e.target.value);
  }
</script>

<div class="flex flex-col gap-1">
  {#if label}
    <label class="text-sm font-medium text-gray-700">{label}</label>
  {/if}
  <input
    type="datetime-local"
    class="border rounded px-2 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-blue-400"
    {placeholder}
    {disabled}
    value={internalValue}
    on:input={onInput} />
</div>
