<script>
  import { onMount } from 'svelte';
  import { Loader, Image as ImageIcon, X, ClipboardCopy, ClipboardCheck } from 'lucide-svelte';
  import ApiClient from '$lib/api';
  import RichTextEditor from '@components/RichTextEditor.svelte';
  // import { htmlToPlainText } from '$lib/TextUtils.js';
  import Info from '@components/business/Info.svelte';

  export let id = '';
  let object = {};
  let findings = '';

  $: if (id) {
    fetchDocuments();
  }

  let documents = [];
  let loading = false;
  let error = '';
  const api = new ApiClient();
  let copied = false;

  async function fetchDocuments() {
    loading = true;
    error = '';
    try {
      //console.log('Fetching documents for id:', id);
      const res = await api.get(`/result-documents/${id}`);
      if (res.success) {
        documents = res.data;
        object = res.object || {};
        findings = object.findings || '';
      } else {
        error = res.error || 'Không tải được dữ liệu';
      }
      //console.log('Obj:', res?.object);
    } catch (err) {
      console.error(err);
      error = 'Lỗi kết nối server';
    } finally {
      loading = false;
    }
  }

  async function copyFindings() {
    if (!findings) return;

    try {
      // copy HTML (giữ định dạng)
      await navigator.clipboard.write([
        new ClipboardItem({
          'text/html': new Blob([findings], { type: 'text/html' }),
          'text/plain': new Blob([findings.replace(/<[^>]+>/g, '')], { type: 'text/plain' })
        })
      ]);

      copied = true;
      // Sau 2 giây reset về lại nút Copy
      setTimeout(() => (copied = false), 2000);
    } catch (err) {
      console.error('Clipboard write failed:', err);
      // fallback copy plain text
      try {
        await navigator.clipboard.writeText(findings.replace(/<[^>]+>/g, ''));
        alert('Copied plain text ✅');
      } catch (e) {
        console.error('Clipboard fallback failed:', e);
      }
    }
  }

  onMount(fetchDocuments);
</script>

<div class="w-full h-full m-0 p-0 bg-orange-300 gap-2 overflow-y-auto">
  <Info bind:data={object} {documents} {loading} {error}>
    <svelte:fragment slot="copy-findings">
      <button
        class="p-2 bg-gray-50 hover:bg-gray-100 rounded flex items-center gap-1"
        on:click={copyFindings}
        title="Copy findings">
        {#if copied}
          <ClipboardCheck size={16} />
          Copied!
        {:else}
          <ClipboardCopy size={16} />
          Copy
        {/if}
      </button>
    </svelte:fragment>
  </Info>
</div>
