<script>
  import { onMount } from 'svelte';
  import {
    Bold,
    Italic,
    Underline,
    Copy,
    Scissors,
    ClipboardPaste,
    Search,
    Replace,
    ChevronDown,
    ChevronUp,
    TextAlignStart as AlignLeft,
    TextAlignCenter as AlignCenter,
    TextAlignEnd as AlignRight,
    TextAlignJustify as AlignJustify,
    X,
    Strikethrough,
    AlignVerticalSpaceAround as LineSpacing
  } from 'lucide-svelte';

  let editor;
  export let value = '';

  let fontFamily = 'Arial';
  let fontSize = '12pt';
  let align = 'justifyLeft';
  let lineHeight = '1.5'; // mặc định 1.5
  let lineHeights = ['0.1', '0.5', '1', '1.15', '1.5', '2', '2.5', '3'];

  let showSearch = false;
  let expanded = false;

  function toggleSearch() {
    showSearch = !showSearch;
  }

  function toggleExpand() {
    expanded = !expanded;
  }

  // @ts-ignore
  function setLineHeight(val) {
    const selection = window.getSelection();
    if (!selection.rangeCount) return;

    const range = selection.getRangeAt(0);
    const container = editor; // contenteditable div

    // kiểm tra xem selection có nằm trong editor không
    if (!container.contains(range.commonAncestorContainer)) return;

    // lấy node cha gần nhất là <p> hoặc <div>
    let el =
      range.commonAncestorContainer.nodeType === 1
        ? range.commonAncestorContainer
        : range.commonAncestorContainer.parentElement;

    // chỉ apply trong editor
    if (container.contains(el)) {
      el.style.lineHeight = val;
      lineHeight = val;
    }
  }

  // gán phím tắt Ctrl+F để mở Search (chặn search mặc định của browser)
  function handleKey(e) {
    if (e.ctrlKey && e.key === 'f') {
      e.preventDefault();
      showSearch = true;
      // focus vào input search khi mở
      setTimeout(() => {
        const el = document.getElementById('findInput');
        if (el) el.focus();
      }, 0);
    }
    if (e.key === 'Escape') {
      showSearch = false;
    }
  }

  const fontSizeMap = {
    '8pt': 1,
    '10pt': 2,
    '12pt': 3,
    '14pt': 4,
    '18pt': 5,
    '24pt': 6,
    '36pt': 7
  };
  const fontSizes = Object.keys(fontSizeMap);

  const fontFamilies1 = ['Arial', 'Times New Roman', 'Courier New', 'Verdana', 'Tahoma', 'Georgia'];
  const fontFamilies = [
    'Arial, sans-serif',
    'Times New Roman, serif',
    'Courier New, monospace',
    'Georgia, serif',
    'Verdana, sans-serif',
    'Roboto, sans-serif',
    'Open Sans, sans-serif'
  ];

  function applyFontSize() {
    const sizeNum = fontSizeMap[fontSize] || 3;
    document.execCommand('fontSize', false, sizeNum);

    const sel = window.getSelection();
    if (sel.rangeCount > 0) {
      const el = sel.anchorNode.parentElement;
      if (el) el.style.fontSize = fontSize;
    }
  }

  function applyFontFamily() {
    document.execCommand('fontName', false, fontFamily);
  }

  //   function format(cmd) {
  //     document.execCommand(cmd, false, null);
  //   }

  function applyAlign(cmd) {
    document.execCommand(cmd, false, null);
    align = cmd;
  }

  function format(cmd, val = null) {
    document.execCommand(cmd, false, val);
    syncValue();
  }
  function syncValue() {
    value = editor.innerHTML;
  }

  // Find & Replace
  let findText = '';
  let replaceText = '';
  //   function doReplace() {
  //     if (!findText) return;
  //     const regex = new RegExp(findText, 'g');
  //     editor.innerHTML = editor.innerHTML.replace(regex, replaceText);
  //     syncValue();
  //   }

  // 🔍 tìm kiếm (highlight tạm)
  function doSearch() {
    if (!editor || !findText) return;

    const content = editor.innerHTML;
    const regex = new RegExp(`(${escapeRegExp(findText)})`, 'gi');
    const highlighted = content.replace(regex, `<mark class="bg-yellow-300">$1</mark>`);
    editor.innerHTML = highlighted;
    syncValue();
  }

  // 🔄 replace lần đầu tiên tìm thấy
  function doReplace() {
    if (!editor || !findText) return;
    editor.innerHTML = editor.innerHTML.replace(findText, replaceText);
    syncValue();
  }

  // 🔁 replace toàn bộ
  function doReplaceAll() {
    if (!editor || !findText) return;

    const regex = new RegExp(escapeRegExp(findText), 'g');
    editor.innerHTML = editor.innerHTML.replace(regex, replaceText);
    syncValue();
  }

  // escape regex để an toàn
  function escapeRegExp(string) {
    return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
  }

  function copyText() {
    const sel = window.getSelection();
    if (!sel || sel.toString() === '') return;

    const text = sel.toString();
    // thử Clipboard API
    if (navigator.clipboard && navigator.clipboard.writeText) {
      navigator.clipboard.writeText(text).catch((err) => {
        console.warn('Clipboard writeText failed:', err);
      });
    } else {
      // fallback execCommand
      try {
        document.execCommand('copy');
      } catch (e) {
        console.warn('execCommand copy failed', e);
      }
    }
  }

  function cutText() {
    const sel = window.getSelection();
    if (!sel || sel.toString() === '') return;

    const text = sel.toString();
    if (navigator.clipboard && navigator.clipboard.writeText) {
      navigator.clipboard
        .writeText(text)
        .then(() => {
          // xóa text đã chọn
          const range = sel.getRangeAt(0);
          range.deleteContents();
        })
        .catch((err) => {
          console.warn('Clipboard cut failed:', err);
        });
    } else {
      try {
        document.execCommand('cut');
      } catch (e) {
        console.warn('execCommand cut failed', e);
      }
    }
  }

  async function pasteText() {
    if (navigator.clipboard && navigator.clipboard.readText) {
      try {
        const text = await navigator.clipboard.readText();
        // chèn text vào vị trí con trỏ
        const sel = window.getSelection();
        if (!sel.rangeCount) return;
        const range = sel.getRangeAt(0);
        range.deleteContents();
        range.insertNode(document.createTextNode(text));
        // move caret sau text mới
        range.collapse(false);
        sel.removeAllRanges();
        sel.addRange(range);
      } catch (err) {
        console.warn('Clipboard paste failed:', err);
      }
    } else {
      try {
        document.execCommand('paste');
      } catch (e) {
        console.warn('execCommand paste failed', e);
      }
    }
  }

  onMount(() => {
    window.addEventListener('keydown', handleKey);
    return () => window.removeEventListener('keydown', handleKey);
  });
</script>

<div class="w-full border border-gray-400 rounded shadow-sm overflow-hidden">
  <!-- Toolbar -->
  <div class="flex flex-wrap gap-4 bg-gray-100 p-2 text-sm">
    <!-- Group: Formatting -->
    <div class="flex items-center gap-1 border-r border-r-gray-300 pr-3">
      <!-- Font family -->
      <select bind:value={fontFamily} on:change={applyFontFamily} class="border rounded px-2 py-1 w-[70px]">
        {#each fontFamilies as fam}
          <option value={fam}>{fam}</option>
        {/each}
      </select>

      <!-- Font size -->
      <select bind:value={fontSize} on:change={applyFontSize} class="border rounded px-2 py-1">
        {#each fontSizes as size}
          <option value={size}>{size}</option>
        {/each}
      </select>

      <!-- Bold / Italic / Underline -->
      <button class="p-1 border rounded hover:bg-gray-100" on:click={() => format('bold')}>
        <Bold size={16} />
      </button>
      <button class="p-1 border rounded hover:bg-gray-100" on:click={() => format('italic')}>
        <Italic size={16} />
      </button>
      <button class="p-1 border rounded hover:bg-gray-100" on:click={() => format('underline')}>
        <Underline size={16} />
      </button>
      <button class="p-1 border rounded hover:bg-gray-100" on:click={() => format('strikeThrough')}>
        <Strikethrough size={16} />
      </button>
    </div>
    <div class="flex items-center gap-1 border-r border-r-gray-300 pr-3">
      <!-- Align -->
      <button
        class="p-1 border rounded hover:bg-gray-100"
        class:bg-gray-200={align === 'justifyLeft'}
        on:click={() => applyAlign('justifyLeft')}>
        <AlignLeft size={16} />
      </button>
      <button
        class="p-1 border rounded hover:bg-gray-100"
        class:bg-gray-200={align === 'justifyCenter'}
        on:click={() => applyAlign('justifyCenter')}>
        <AlignCenter size={16} />
      </button>
      <button
        class="p-1 border rounded hover:bg-gray-100"
        class:bg-gray-200={align === 'justifyRight'}
        on:click={() => applyAlign('justifyRight')}>
        <AlignRight size={16} />
      </button>
      <button
        class="p-1 border rounded hover:bg-gray-100"
        class:bg-gray-200={align === 'justifyFull'}
        on:click={() => applyAlign('justifyFull')}>
        <AlignJustify size={16} />
      </button>

      <input type="color" class="w-6 h-6 border rounded" on:input={(e) => format('foreColor', e.target.value)} />
    </div>

    <!-- Line spacing -->
    <div class="flex items-center gap-1 border-r border-r-gray-300 pr-3">
      <div class="flex items-center gap-1 p-1 border rounded hover:bg-gray-100">
        <LineSpacing class="" size={16} />
        <select
          class="border rounded px-1 text-sm"
          bind:value={lineHeight}
          on:change={(e) => setLineHeight(e.target.value)}>
          {#each lineHeights as line}
            <option value={line}>{line}</option>
          {/each}
        </select>
      </div>
    </div>

    <!-- Group: Clipboard -->
    <div class="flex items-center gap-1 border-r border-r-gray-300 pr-3">
      <button class="p-1 hover:bg-gray-200 rounded" title="Copy (Ctrl+C)" on:click={copyText}>
        <Copy size={16} />
      </button>
      <button class="p-1 hover:bg-gray-200 rounded" title="Cut (Ctrl+X)" on:click={cutText}>
        <Scissors size={16} />
      </button>
      <button class="p-1 hover:bg-gray-200 rounded" title="Paste (Ctrl+V)" on:click={pasteText}>
        <ClipboardPaste size={16} />
      </button>
    </div>

    <!-- Toolbar có nút search -->
    <div class="flex items-center">
      <button class="p-1 rounded hover:bg-gray-200" on:click={toggleSearch} title="Toggle Search (Ctrl+F)">
        <Search size={14} />
      </button>
    </div>
    {#if showSearch}
      <!-- Group: Search -->
      <div class="flex flex-col gap-1 mt-3">
        <!-- Hàng 1: Find + toggle -->
        <div class="flex items-center gap-1">
          <Search size={14} class="text-gray-500" />
          <input
            class="border p-1 hover:bg-gray-200 rounded"
            placeholder="Find..."
            bind:value={findText}
            on:keydown={(e) => e.key === 'Enter' && doSearch()} />

          <button class="p-1 hover:bg-gray-200 rounded" on:click={toggleExpand}>
            {#if expanded}
              <ChevronUp size={14} />
            {:else}
              <ChevronDown size={14} />
            {/if}
          </button>

          <button class="border p-1 hover:bg-gray-200 rounded" on:click={doSearch}>Find</button>
        </div>

        <!-- Hàng 2: Replace (ẩn/hiện) -->
        {#if expanded}
          <div class="flex items-center gap-1 animate-fade-in">
            <Replace size={14} class="text-gray-500" />
            <input
              class="border p-1 hover:bg-gray-200 rounded"
              placeholder="Replace..."
              bind:value={replaceText}
              on:keydown={(e) => e.key === 'Enter' && doReplace()} />

            <button class="border p-1 hover:bg-gray-200 rounded" on:click={doReplace}>Replace</button>

            <button class="border p-1 hover:bg-gray-200 rounded" on:click={doReplaceAll}>Replace All</button>
          </div>
        {/if}
      </div>
      <button on:click={toggleSearch} class="hover:text-red-500">
        <X size={16} />
      </button>
    {/if}
  </div>

  <!-- Editor -->
  <div
    bind:this={editor}
    class="prose max-w-none p-3 min-h-[150px] outline-none bg-white"
    style="text-align:left"
    contenteditable="true"
    bind:innerHTML={value}
    on:input={syncValue}>
  </div>
</div>

<style>
  /* animation mở rộng */
  .animate-fade-in {
    animation: fadeIn 0.2s ease-in-out;
  }
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-2px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
</style>
